const addNew = document.querySelector("#add-new");
const clearList = document.querySelector("#clear-list");
const removeComplete = document.querySelector("#remove-complete");
const navigationBar = document.querySelector(".nav--box");
const inputField = document.querySelector(".add-item--field");
const itemContainer = document.querySelector(".item--container");
let items = document.querySelectorAll(".item");

class Item {
  complete = false;
  constructor(name) {
    this.name = name;
  }
}

let shoppingList = [];
// console.log(localStorage.getItem("list"));

// function storeToLocalStorage() {
//   localStorage.setItem("list", JSON.stringify(shoppingList));
// }

function toggleNavigation(boo) {
  if (boo) {
    navigationBar.classList.remove("hidden");
    inputField.classList.add("hidden");
  } else {
    navigationBar.classList.add("hidden");
    inputField.classList.remove("hidden");
  }
}

function renderList(list) {
  itemContainer.innerHTML = "";
  if (shoppingList.length === 0) {
    const html = `<h1 class="empty-tag">No items yet</h1>`;
    itemContainer.insertAdjacentHTML("afterbegin", html);
  } else {
    list.forEach((el) => {
      const html = `<div class="item" data-complete="false"><div class='checkbox'></div><h1 class='item--text'>${el.name}</h1></div>`;
      itemContainer.insertAdjacentHTML("beforeend", html);
    });
  }
}

renderList(shoppingList);

addNew.addEventListener("click", function () {
  toggleNavigation(false);
  inputField.focus();
});

function addNewItem(name) {
  const arrayOfWords = name.split(" ");
  let capitalized = "";
  arrayOfWords.forEach((word) => {
    capitalized += word.slice(0, 1).toUpperCase();
    capitalized += word.slice(1, word.length).toLowerCase();
    capitalized += " ";
  });
  shoppingList.push(new Item(capitalized));
}

// addNewItem("coke coca cola");

inputField.addEventListener("keydown", function (e) {
  if (e.key === "Enter") {
    // shoppingList.push(new Item(inputField.value, false));
    addNewItem(inputField.value);
    // storeToLocalStorage();
    inputField.value = "";
    items = document.querySelectorAll(".item");
    renderList(shoppingList);
    toggleNavigation(true);
  }
});

clearList.addEventListener("click", function () {
  shoppingList = [];
  renderList(shoppingList);
});

