itemContainer.addEventListener("click", function (event) {
  let target = event.target;
  if (target.classList.contains("item--text")) {
    target = target.parentNode;
  }
  shoppingList.forEach((el) => {
    if (el.name === target.textContent) {
      if (el.complete === false) {
        el.complete = true;
        target.classList.add("item-complete");
        target.children[0].classList.add("checkbox-complete");
      } else {
        el.complete = false;
        target.classList.remove("item-complete");
        target.children[0].classList.remove("checkbox-complete");
      }
    }
  });
});

function sortDone(list) {
  const newArr = [];
  list.forEach((el) => {
    if (el.complete === false) {
      newArr.push(el);
    }
  });
  return newArr;
}

removeComplete.addEventListener("click", function () {
  shoppingList = sortDone(shoppingList);
  renderList(shoppingList);
});
